/** @format */

:root {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
		"Helvetica Neue", sans-serif;
}

*,
::after,
::before {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	box-sizing: border-box;
	font-family: sans-serif;
	line-height: 1.15;
}

main {
	display: block;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: normal;
}

p + p {
	margin-top: 1rem;
}

a {
	background-color: transparent;
	color: inherit;
	text-decoration: none;
}

abbr[title] {
	text-decoration: underline dotted;
}

code,
kbd,
pre,
samp {
	font-family: monospace;
	font-size: 1em;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}

button,
input,
optgroup,
select,
textarea {
	border: 1px solid currentColor;
	font-family: inherit;
	font-size: 100%;
	line-height: inherit;
	margin: 0;
}

button {
	border: none;
	overflow: visible;
	text-transform: none;
}

[type="button"],
[type="reset"],
[type="submit"],
button {
	appearance: button;
}

input {
	overflow: visible;
}

input,
textarea {
	padding: 1px;
}

fieldset {
	border: 1px solid currentColor;
	margin: 0 2px;
}

legend {
	color: inherit;
	display: table;
	max-width: 100%;
	padding: 0;
	white-space: normal;
}

progress {
	display: inline-block;
	vertical-align: baseline;
}

select {
	text-transform: none;
}

textarea {
	overflow: auto;
	vertical-align: top;
}

[type="search"] {
	appearance: textfield;
	outline-offset: -2px;
}

[type="color"] {
	background: inherit;
}

hr {
	box-sizing: content-box;
	color: inherit;
	height: 0;
	overflow: visible;
}

dl,
ol,
ul {
	list-style: none;
	margin: 0;
}

dl dl,
dl ol,
dl ul,
ol dl,
ol ol,
ol ul,
ul dl,
ul ol,
ul ul {
	margin: 0;
}

b,
strong {
	font-weight: bolder;
}

audio,
video {
	display: inline-block;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

img {
	border: 0;
}

img,
video {
	height: auto;
	max-width: 100%;
}

iframe {
	border: 0;
}

svg:not(:root) {
	overflow: hidden;
}

table {
	border-collapse: collapse;
	border-color: inherit;
	border-spacing: 0;
	text-indent: 0;
}

details {
	display: block;
}

dialog {
	background-color: inherit;
	border: solid;
	color: inherit;
	display: block;
	height: fit-content;
	left: 0;
	margin: auto;
	padding: 1em;
	position: absolute;
	right: 0;
	width: fit-content;
}

dialog:not([open]) {
	display: none;
}

summary {
	display: list-item;
}

canvas {
	display: inline-block;
}

template {
	display: none;
}

[hidden] {
	display: none;
}
